ul.breadcrumb {
  list-style: none;
  padding-top: 45px;
}

ul.breadcrumb li {
  display: inline;
  font-weight: 400;
  font-size: 13px;
  line-height: 27px;
  color: var(--black-color);
  opacity: 0.8;
}

ul.breadcrumb li + li:before {
  padding: 8px;
  color: black;
  content: "/\00a0";
}

ul.breadcrumb li a {
  color: #0275d8;
  text-decoration: none;
}

ul.breadcrumb li a:hover {
  color: #01447e;
  text-decoration: underline;
}
