.services__page {
  padding: 40px 0 100px 0;

  &-left {
    background-color: #f7f7f7;
    border-radius: 10px;
    padding: 45px 40px;

    &-title {
      margin: 0 0 35px 0;
      font-weight: 900;
      font-size: 32px;
      line-height: 27px;
      color: var(--black-color);
      opacity: 0.8;
    }

    .list-reset {
      li {
        a {
          font-weight: 400;
          font-size: 16px;
          line-height: 24px;
          color: var(--black-color);
          opacity: 0.8;

        }

        .active-link {
          color: var(--btn-color);
        }

        &:not(:last-child) {
          margin-bottom: 20px;
        }
      }
    }
  }

  @media screen and (max-width: 1200px) {
    &-left {
      padding: 30px;

      &-title {
        margin: 0 0 25px 0;
        font-size: 28px;
        line-height: 24px;
      }

      .list-reset {
        li {
          &:not(:last-child) {
            margin-bottom: 15px;
          }
        }
      }
    }
  }

  @media screen and (max-width: 992px) {
    &-left {
      padding: 15px;

      &-title {
        margin: 0 0 15px 0;
        font-size: 22px;
        line-height: 20px;
      }

      .list-reset {
        li {
          &:not(:last-child) {
            margin-bottom: 10px;
          }
        }
      }
    }
  }

  @media screen and (max-width: 768px) {
    &-left {
      margin-bottom: 20px;
    }
  }

  &-right {

    &-box {
      min-height: 380px;
      border-radius: 10px;
      position: relative;
      overflow: hidden;
      display: flex;
      align-items: flex-end;

      &-title {
        max-width: 314px;
        font-weight: 800;
        font-size: 36px;
        line-height: 44px;
        color: var(--light-color);
        position: relative;
        z-index: 1;
        margin: 0 0 60px 50px;
      }

      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: var(--btn-color);
        opacity: 0.7;
      }
    }

    &-desc {
      margin: 55px 0 0 0;
      font-weight: 700;
      font-size: 24px;
      line-height: 36px;
      color: #000000;
      opacity: 0.8;
    }

    &-list {
      display: flex;
      justify-content: space-between;
      column-gap: 50px;
      margin: 60px 0 0 0;

      .list-reset {
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;

        li {
          flex-grow: 2;

          p {
            margin: 0;
            font-weight: 700;
            font-size: 16px;
            line-height: 24px;
            color: var(--black-color);
            opacity: 0.8;
            padding-left: 20px;
            position: relative;

            &:after {
              position: absolute;
              content: '';
              top: 30%;
              left: 0;
              width: 6px;
              height: 6px;
              border-radius: 50%;
              background-color: var(--light-color);
              border: 2px solid var(--btn-color);
            }
          }

          &:not(:last-child) {
            margin-bottom: 20px;
          }
        }
      }
    }
  }

  @media screen and (max-width: 768px) {
    &-right {

      &-box {

        &-title {
          font-size: 32px;
          line-height: 40px;
          margin: 0 0 40px 30px;
        }

      }

      &-desc {
        margin: 40px 0 0 0;
        font-size: 22px;
        line-height: 36px;
      }

      &-list {
        margin: 40px 0 0 0;
        flex-direction: column;

        .list-reset {

          li {
            &:not(:last-child) {
              margin-bottom: 15px;
            }
          }
        }
      }
    }
  }

  @media screen and (max-width: 576px) {
    &-right {

      &-box {
        min-height: 280px;

        &-title {
          font-size: 22px;
          line-height: 35px;
          margin: 0 0 35px 25px;
        }

      }

      &-desc {
        margin: 30px 0 0 0;
        font-size: 18px;
        line-height: 30px;
      }

      &-list {
        margin: 35px 0 0 0;
        flex-direction: column;

        .list-reset {

          li {
            &:not(:last-child) {
              margin-bottom: 10px;
            }
          }
        }
      }
    }
  }

}
