.projects__in {
  &-title {
    padding-top: 45px;

    h4 {
      margin: 0;
      font-weight: 800;
      font-size: 36px;
      line-height: 44px;
      color: var(--black-color);
    }
  }

  @media screen and (max-width: 768px) {
    &-title {
      padding-top: 35px;

      h4 {
        margin: 0;
        font-size: 32px;
        line-height: 40px;
      }
    }
  }

  &-info {
    &-box {
      display: flex;
      justify-content: space-between;
      align-items: center;
      border-radius: 20px;
      position: relative;
      overflow: hidden;
      min-height: 480px;
      margin-top: 55px;
      padding: 0 65px 0 65px;

      &-text {
        width: 50%;
        max-width: 100%;
        z-index: 2;

        p {
          margin: 45px 0 55px 0;
          font-weight: 400;
          font-size: 16px;
          line-height: 24px;
          color: var(--light-color);
          opacity: 0.8;
        }

        &-btn {
          font-weight: 400;
          font-size: 14px;
          line-height: 17px;
          color: var(--light-color);
          background-color: #99c2d7;
          box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
          border-radius: 10px;
          padding: 20px 40px;
        }

        .list-reset {
          max-width: 400px;

          li {
            &:not(:last-child) {
              margin-bottom: 20px;
            }
          }

          h3 {
            margin: 0;
            font-weight: 700;
            font-size: 16px;
            line-height: 24px;
            color: var(--light-color);
            opacity: 0.8;
          }

          p {
            margin: 0;
            font-weight: 400;
            font-size: 16px;
            line-height: 24px;
            color: var(--light-color);
            opacity: 0.8;
          }

          .circle {
            position: relative;
            padding-left: 20px;

            &:after {
              content: '';
              position: absolute;
              left: 0;
              top: 30%;
              width: 6px;
              height: 6px;
              background-color: var(--light-color);
              border-radius: 50%;
            }
          }
        }
      }

      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: var(--btn-color);
        opacity: 0.8;
      }
    }

    &-text {
      padding: 60px 50px 180px 50px;

      &-title {
        margin: 0;
        font-weight: 700;
        font-size: 24px;
        line-height: 36px;
        color: var(--black-color);
        opacity: 0.8;
      }

      &-list {
        display: flex;
        justify-content: space-between;
        column-gap: 50px;
        margin: 60px 0 60px 0;

        .list-reset {
          display: flex;
          flex-direction: column;
          flex-wrap: wrap;

          li {
            flex-grow: 2;

            p {
              margin: 0;
              font-weight: 700;
              font-size: 16px;
              line-height: 24px;
              color: var(--black-color);
              opacity: 0.8;
              padding-left: 20px;
              position: relative;

              &:after {
                position: absolute;
                content: '';
                top: 30%;
                left: 0;
                width: 6px;
                height: 6px;
                border-radius: 50%;
                background-color: var(--light-color);
                border: 2px solid var(--btn-color);
              }
            }

            &:not(:last-child) {
              margin-bottom: 20px;
            }
          }
        }
      }


      &-desc {
        margin: 0;
        font-weight: 400;
        font-size: 20px;
        line-height: 27px;
        color: var(--black-color);
        opacity: 0.8;

        &:not(:last-child) {
          margin-bottom: 20px;
        }
      }
    }
  }

  @media screen and (max-width: 768px) {
    &-info {
      &-box {
        margin-top: 45px;
        padding: 15px 20px 15px 20px;
        flex-direction: column;

        &-text {
          width: 100%;
          max-width: 100%;
          z-index: 2;
          justify-content: flex-start !important;

          p {
            margin: 20px 0 20px 0;
            font-size: 16px;
            line-height: 24px;
            text-align: center;
          }

          .circle {
            text-align: left;
          }

          &-btn {
            padding: 15px 30px;
            display: block;
            margin: 0 auto;
          }

          .list-reset {

            li {
              &:not(:last-child) {
                margin-bottom: 15px;
              }
            }

          }
        }

      }

      &-text {
        padding: 60px 0 140px 0;

        &-title {
          font-size: 22px;
          line-height: 30px;
        }

        &-list {
          margin: 40px 0 40px 0;
          flex-direction: column;
        }


        &-desc {
          font-size: 18px;
          line-height: 20px;

          &:not(:last-child) {
            margin-bottom: 15px;
          }
        }
      }
    }
  }

  @media screen and (max-width: 576px) {
    &-info {
      &-box {
        padding: 15px;

        &-text {

          &-btn {
            margin: 0 auto 10px;
          }

        }

      }

      &-text {
        padding: 60px 0 80px 0;

        &-title {
          font-size: 18px;
          line-height: 30px;
        }

        &-list {
          margin: 35px 0 35px 0;
        }
      }
    }
  }
}
